.filterContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 50px auto 50px auto;
  width: 70%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.14);
  backdrop-filter: blur(9px);
  -webkit-backdrop-filter: blur(9px);
  color: white;
  font-family: "Roboto", sans-serif;
  font-size: 1.5rem;
  font-weight: 500;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
}

.selectorContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0px 20px;
  background: none;
  border: none;
  color: aqua;
  font-size: 20px;
}

.filterSelector {
  margin: 0px 20px;
  background: rgba(255, 255, 255, 0.329);
  height: 75px;
  border: none;
  border-radius: 8px;
  color: aqua;
  font-size: 20px;
}

.filterSelector optgroup {
  background-color: darkgray;
  color: white;
}
.filterSelector option {
  background: grey;
  color: white;
}
